<template>
  <PageLayout title="SQL">
    <el-row :gutter="32" class="sql-format-content">
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="input-section" shadow="hover">
          <div slot="header" class="card-header">
            <span class="card-title">
              <i class="el-icon-edit"></i>
              SQL
            </span>
          </div>

          <el-input v-model="sqlInput" type="textarea" :rows="15" placeholder="SQL..." resize="none"
            class="sql-input" />

          <div class="button-group">
            <el-button type="primary" @click="convertSql" icon="el-icon-refresh">
              
            </el-button>
            <el-button @click="clearAll" icon="el-icon-delete">
              
            </el-button>
            <el-button @click="loadTestData" icon="el-icon-document">
              
            </el-button>
          </div>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="output-section" shadow="hover">
          <div slot="header" class="card-header">
            <span class="card-title">
              <i class="el-icon-document"></i>
              
            </span>
          </div>

          <el-input v-model="showSql" type="textarea" :rows="15" placeholder="..." readonly resize="none"
            class="sql-output" />

          <div class="button-group">
            <el-button type="success" @click="copyResult" icon="el-icon-document-copy">
              
            </el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </PageLayout>
</template>

<script>
import PageLayout from '@/components/PageLayout.vue';

export default {
  name: 'SqlFormat',
  components: {
    PageLayout
  },
  data() {
    return {
      sqlInput: '',
      showSql: ''
    };
  },
  methods: {
    // SQL
    convertSql() {
      if (!this.sqlInput.trim()) {
        this.$message.warning('SQL');
        return;
      }

      try {
        const result = this.parseSqlFields(this.sqlInput);
        this.showSql = result.join('\n');
        this.$message.success('');
      } catch (error) {
        this.$message.error('');
        console.error(':', error);
      }
    },

    // SQL
    parseSqlFields(sqlInput) {
      // 
      const cleanedInput = sqlInput.trim();

      // 
      const fieldDefinitions = this.splitFieldDefinitions(cleanedInput);

      return fieldDefinitions
        .filter(field => field.trim())
        .map(field => this.parseSingleField(field))
        .filter(result => result); // 
    },

    // 
    splitFieldDefinitions(input) {
      // 
      const trimmedInput = input.replace(/,\s*$/, '');

      // 
      const fields = [];
      let currentField = '';
      let inQuotes = false;
      let quoteChar = '';

      for (let i = 0; i < trimmedInput.length; i++) {
        const char = trimmedInput[i];

        if ((char === "'" || char === '"') && (i === 0 || trimmedInput[i - 1] !== '\\')) {
          if (!inQuotes) {
            inQuotes = true;
            quoteChar = char;
          } else if (char === quoteChar) {
            inQuotes = false;
          }
        }

        if (char === ',' && !inQuotes) {
          fields.push(currentField.trim());
          currentField = '';
        } else {
          currentField += char;
        }
      }

      if (currentField.trim()) {
        fields.push(currentField.trim());
      }

      return fields;
    },

    // 
    parseSingleField(fieldDefinition) {
      try {
        // 
        const fieldNameMatch = fieldDefinition.match(/`([^`]+)`/);
        if (!fieldNameMatch) {
          console.warn(':', fieldDefinition);
          return null;
        }

        const fieldName = fieldNameMatch[1];
        const camelCaseName = this.strToCam(fieldName);

        // 
        const typeMatch = fieldDefinition.match(/`\s*(\w+)/i);
        const dataType = typeMatch ? typeMatch[1].toLowerCase() : '';

        // 
        const numberTypes = ['bigint', 'int', 'tinyint', 'smallint', 'mediumint', 'decimal', 'float', 'double'];
        const finalType = numberTypes.includes(dataType) ? 'number' : 'string';

        // 
        const isRequired = fieldDefinition.includes('NOT NULL');

        // 
        const defaultValueMatch = fieldDefinition.match(/DEFAULT\s+['"`]?([^'"`,\s]+)['"`]?/i);
        const defaultValue = defaultValueMatch ? defaultValueMatch[1] : null;

        // 
        const commentMatch = fieldDefinition.match(/COMMENT\s+['"`]([^'"`]+)['"`]/i);
        const comment = commentMatch ? commentMatch[1] : '';

        // 
        const commentParts = [];
        if (comment) commentParts.push(comment);
        if (isRequired) commentParts.push('');
        if (defaultValue !== null) commentParts.push(`${ defaultValue }`);

        const fullComment = commentParts.length > 0 ? `  // ${ commentParts.join(' ') }` : '';

        // 
        const requiredMark = isRequired ? '' : '?';
        return `${ camelCaseName }${ requiredMark }: ${ finalType };${ fullComment }`;

      } catch (error) {
        console.warn(':', fieldDefinition, error);
        return null;
      }
    },

    // 
    strToCam(str) {
      if (!str || typeof str !== 'string') return '';

      return str
        .toLowerCase()
        .split('_')
        .map((word, index) => {
          if (index === 0) return word;
          return word.charAt(0).toUpperCase() + word.slice(1);
        })
        .join('');
    },

    // 
    copyResult() {
      if (!this.showSql.trim()) {
        this.$message.warning('');
        return;
      }

      navigator.clipboard.writeText(this.showSql).then(() => {
        this.$message.success('');
      }).catch(() => {
        // 
        const textArea = document.createElement('textarea');
        textArea.value = this.showSql;
        document.body.appendChild(textArea);
        textArea.select();
        document.execCommand('copy');
        document.body.removeChild(textArea);
        this.$message.success('');
      });
    },

    // 
    clearAll() {
      this.sqlInput = '';
      this.showSql = '';
    },

    // 
    loadTestData() {
      this.sqlInput = `
        \`id\` bigint(20) NOT NULL AUTO_INCREMENT COMMENT '',
        \`company_id\` bigint(20) NOT NULL COMMENT '',
        \`bill_no\` varchar(32) CHARACTER SET utf8 COLLATE utf8_general_ci NOT NULL COMMENT '',
        \`contract_status\` tinyint(4) NOT NULL DEFAULT '10' COMMENT '(10= 20= 30=)',
        \`display_type\` tinyint(4) NOT NULL DEFAULT '0' COMMENT '/(0= 1=)',
        \`supplier_no\` varchar(20) NOT NULL COMMENT '',
        \`stock_qty\` decimal(12,3) NOT NULL DEFAULT '0.000' COMMENT '',
        \`before_qty\` decimal(12,3) NOT NULL DEFAULT '0.000' COMMENT '',
        \`adjustment_qty\` decimal(12,3) NOT NULL DEFAULT '0.000' COMMENT '',
        \`settlement_method\` tinyint(4) NOT NULL COMMENT ' 1= 2= 3= ',
        \`currency\` tinyint(4) NOT NULL COMMENT '(1= 2= 3=)',
        \`effective_date\` date NOT NULL COMMENT '',
        \`status\` varchar(20) NOT NULL DEFAULT '10' COMMENT '(10= 20=)',
        \`remarks\` varchar(100) DEFAULT NULL COMMENT '',
        \`creator\` varchar(20) NOT NULL COMMENT '',
        \`create_time\` datetime NOT NULL DEFAULT CURRENT_TIMESTAMP COMMENT '()',
        \`modifier\` varchar(20) DEFAULT NULL COMMENT '',
        \`modify_time\` datetime DEFAULT NULL COMMENT '',
        \`update_time\` datetime DEFAULT CURRENT_TIMESTAMP ON UPDATE CURRENT_TIMESTAMP COMMENT '',
        \`del_tag\` varchar(2) DEFAULT '0' COMMENT '(0= 1=',
        \`partion_no\` varchar(20) NOT NULL COMMENT '',
      `;
      this.showSql = '';
    }
  }
};
</script>

